function Footer(props) {

  const [resourcesLink] = useState([
    { title: 'Github', path: '#' },
    { title: 'Branding', path: '#' },
  ])

  const [websiteLink] = useState([
    { title: 'Privacy Policy', path: '#' },
    { title: 'Cookie Policy', path: '#' },
    { title: 'Terms of Service', path: '#' },
  ])

  const [listSocial] = useState([
    { icon: 'icon-facebook-f', path: '#' },
    { icon: 'icon-instagram', path: '#' },
    { icon: 'icon-youtube', path: '#' },
    { icon: 'icon-twitter', path: '#' },
  ])

  const [isVisible, setIsVisible] = useState(false);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  };
  
  useEffect(() => {
    const toggleVisibility = () => {
      if (window.pageYOffset > 500) {
        setIsVisible(true);
      } 
      else {
        setIsVisible(false);
      }
    }
      window.addEventListener("scroll", toggleVisibility);
    return () => window.removeEventListener("scroll", toggleVisibility);
  }, []);

  return (
    <footer className="footer style-2">
      <div className="container">
        <div className="footer__main">
          <div className="row">
            <div className="col-xl-4 col-md-6">
              <div className="info">

                <h5>resources</h5>
                <ul className="list">
                  {
                    resourcesLink.map((data,idx) => (
                      <li key={idx}><Link to={data.path}><p>{data.title}</p></Link></li>
                    ))
                  }
                </ul> 
                <ul className="list"><br /><br />
                  <Link to="/" className="logo light"><img src={img} alt="" /></Link>
                  <Link to="/" className="logo dark"><img src={img1} alt="" /></Link>
                </ul>

              </div>
            </div>
            <div className="col-xl-4 col-md-6">
              <div className="info">
                <h5>website</h5>
                <ul className="list">
                  {
                    websiteLink.map((data,idx) => (
                      <li key={idx}><Link to={data.path}><p>{data.title}</p></Link></li>
                    ))
                  }
                </ul> 
              </div>
            </div>
            <div className="col-xl-4 col-md-12">
              <div className="footer-contact">
                <h5>newsletter</h5>
                <p>Stay tuned for the latest updates from the Rookie</p>
                <form action="#">
                  <input type="email" placeholder="Enter your email" required="" />
                  <button type="submit" className="btn-action">Submit</button>
                </form>
                <ul className="list-social">
                  {
                    listSocial.map((data,idx) => (
                      <li key={idx}>
                        <Link to={data.path}><span className={data.icon}></span></Link>
                      </li>
                    ))
                  }
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container-fluid">
        <div className="footer__bottom">
          <p>©2022 Rockie.com. All rights reserved. Terms of Service | Privacy Terms</p>
        </div>
      </div>

      {
        isVisible && <Link onClick={scrollToTop}  to='#' id="scroll-top"></Link>
      }
    </footer>
  )
}

export default Footer;