import React , { useState , useEffect } from 'react';
import { Link , NavLink } from 'react-router-dom';
import menus from '../../pages/menu';
import { Dropdown } from 'react-bootstrap';
import './styles.scss';
import logo from '../../assets/images/logo/logo.png';
import logodark from '../../assets/images/logo/logo-dark.png';
import avt from '../../assets/images/avt/avt-01.jpg';
import DarkMode from './DarkMode';
import icon1 from '../../assets/images/flags/us.jpg'
import icon2 from '../../assets/images/flags/spain.jpg'

const Header = () => {
  const [scroll, setScroll] = useState(false)

  useEffect(() => {
    window.addEventListener("scroll", () => { setScroll(window.scrollY > 300) })
    return () => { setScroll({}) }
  }, [])

  const [menuActive, setMenuActive] = useState(null)

  const handleMenuActive = () => {
    setMenuActive(!menuActive)
  }
    
  const [activeIndex, setActiveIndex] = useState(null)
  const handleDropdown = index => {
    setActiveIndex(index)
  }

  return (
    <header id="header_main" className={`header ${scroll ? 'is-fixed' : ''}`}>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <div className="header__body d-flex justify-content-between">
              <div className="header__left">
                <div className="logo">
                <NavLink to='/' className="light">
                    <img
                    src={logo}
                    alt="Rockie"
                    />
                </NavLink>
                <NavLink to='/' className="dark">
                    <img
                    src={logodark}
                    alt="Rockie"
                    />
                </NavLink>
                </div>
                <div className="left__main">
                  <nav id="main-nav" className={`main-nav ${menuActive ? 'active' : ''}`}>
                    <ul id="menu-primary-menu" className="menu">
                    {
                      menus.map((data,idx) => (
                        <li key={idx} onClick={()=> handleDropdown(idx)} className={`menu-item ${data.namesub ? 'menu-item-has-children' : ''} ${activeIndex === idx ? 'active' : ''}`}>
                          <Link to={data.links}>{data.name}</Link>
                          {
                            data.namesub &&
                            <ul className="sub-menu">
                              {
                                data.namesub.map((submenu) => (
                                  <li key={submenu.id} className="menu-item"><NavLink to={submenu.links}>{submenu.sub}</NavLink></li>
                                ))
                              }
                            </ul>
                          }
                        </li>
                      ))
                    }
                    </ul>
                  </nav>

                </div>
              </div>

              <div className="header__right">
                <Dropdown>
                  <Dropdown.Toggle >
                    EN/USD  
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    <Dropdown.Item href="#">
                      <Link to="#" className="dropdown-item notify-item language" data-lang="en" >
                        <img src={icon1} alt="user-image" className="me-1" height="12" />
                        <span className="align-middle">English</span>
                      </Link>
                    </Dropdown.Item>
                    <Dropdown.Item href="#">
                      <Link to="#" className="dropdown-item notify-item language" data-lang="en" >
                        <img src={icon2} alt="user-image" className="me-1" height="12" />
                        <span className="align-middle">English</span>
                      </Link>
                    </Dropdown.Item>
                   </Dropdown.Menu>
                </Dropdown>

                <DarkMode />
                <div className={`mobile-button ${menuActive ? 'active' : ''}`} onClick={handleMenuActive}><span></span></div>
                <div className="wallet">
                  <Link to="/wallet">Wallet</Link>
                </div>
                <Dropdown className='user'>
                  <Dropdown.Toggle >
                    <img src={avt} alt="Rockie" />  
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    <Dropdown.Item href="#">
                      <Link className="dropdown-item" to="#"><i className="bx bx-user font-size-16 align-middle me-1"></i>
                      <span>Profile</span></Link>
                    </Dropdown.Item>
                    <Dropdown.Item href="#">
                      <Link className="dropdown-item" to="#">
                      <i className="bx bx-wallet font-size-16 align-middle me-1"></i>
                      <span>My Wallet</span></Link>
                    </Dropdown.Item>
                    <Dropdown.Item href="#">
                      <Link className="dropdown-item d-block" to="#">
                      <i className="bx bx-wrench font-size-16 align-middle me-1"></i>
                      <span>Settings</span></Link>
                    </Dropdown.Item>
                    <Dropdown.Item href="#">
                      <Link className="dropdown-item text-danger" to="/login">
                      <i className="bx bx-power-off font-size-16 align-middle me-1 text-danger"></i>
                      <span>Logout</span></Link>
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header